
*{-webkit-box-sizing:border-box;box-sizing:border-box
}
*:before,*:after{-webkit-box-sizing:border-box;box-sizing:border-box
}
html{width:100%;height:100%;font-family:"PingFangSC-Regular";-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%;font-size:14px;color:#333;line-height:1;word-wrap:break-word
}
body{position:relative;width:100%;height:100%
}
html,body,div,object,iframe,applet,object,h1,h2,h3,h4,h5,h6,p,blockquote,pre,address,dl,dt,dd,ol,ul,li,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,menu,nav,output,ruby,section,summary,time,mark,audio,video,progress{margin:0;padding:0;border:0;vertical-align:baseline
}
th,td{vertical-align:middle
}
li{list-style:none
}
article,aside,details,figcaption,figure,footer,header,main,menu,nav,section,summary{display:block
}
audio,canvas,progress,video{display:inline-block
}
audio:not([controls]){display:none;height:0
}
[hidden],template{display:none
}
a{background-color:transparent;text-decoration:none
}
a:active,a:hover{outline:0
}
abbr[title]{border-bottom:1px dotted
}
b,strong{font-weight:bold
}
dfn{font-style:italic
}
h1{font-size:2em;margin:.67em 0
}
mark{background:#ff0;color:#000
}
small{font-size:80%
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-0.5em
}
sub{bottom:-0.25em
}
img{border:0
}
figure{margin:1em 40px
}
hr{-webkit-box-sizing:content-box;box-sizing:content-box;height:0
}
pre{overflow:auto
}
code,kbd,pre,samp{font-family:monospace, monospace;font-size:1em
}
button,input,optgroup,select,textarea{color:inherit;font:inherit;margin:0;outline:none
}
button{overflow:visible;border:0
}
button,select{text-transform:none
}
button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:button;cursor:pointer
}
button[disabled],html input[disabled]{cursor:default
}
button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0
}
input{line-height:normal
}
input[type="checkbox"],input[type="radio"]{-webkit-box-sizing:border-box;box-sizing:border-box;padding:0
}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button{height:auto
}
input[type="search"]{-webkit-appearance:textfield;-webkit-box-sizing:content-box;box-sizing:content-box
}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em
}
legend{border:0;padding:0
}
textarea{overflow:auto
}
optgroup{font-weight:bold
}
table{border-collapse:collapse;border-spacing:0
}
td,th{padding:0
}
:focus{outline:none
}
.modal-body::-webkit-scrollbar{width:6px;height:6px
}
.modal-body::-webkit-scrollbar-track{background-color:inherit
}
.modal-body:hover::-webkit-scrollbar-thumb{border-radius:6px;background-color:#C6CCD7
}
.modal-body::-webkit-scrollbar-thumb{background-color:inherit
}
.scroll-box::-webkit-scrollbar{width:6px;height:6px
}
.scroll-box::-webkit-scrollbar-track{background-color:inherit
}
.scroll-box:hover::-webkit-scrollbar-thumb{border-radius:6px;background-color:#C6CCD7
}
.scroll-box::-webkit-scrollbar-thumb{background-color:inherit
}
.el-input-group__append,.el-input-group__prepend{padding:0 12px
}
.el-input-group__append .el-button,.el-input-group__prepend .el-button{padding:0 12px
}
.el-input-group__append div.el-select .el-input--suffix .el-input__inner{padding-left:12px;padding-right:25px
}
.el-input-group__append .el-button,.el-input-group__append .el-select,.el-input-group__prepend .el-button,.el-input-group__prepend .el-select{margin:-10px -12px
}
.el-tabs .el-tabs__header{margin-bottom:0
}
.el-tabs .el-tabs__nav-wrap{padding-left:20px
}
.el-table{border:1px solid #DDDDDD
}
.el-table th,.el-table thead.is-group th{background-color:#E2E8F4;color:#657BAF
}
.el-table::before{height:0
}
.el-table tr:last-child td{border-bottom:none
}
.el-icon-loading{font-size:30px
}
.el-table .el-badge__content.is-fixed.is-dot{right:10px;top:10px
}
.el-card{border-radius:0;-webkit-box-shadow:0px 0px 8px 0px rgba(0,0,0,0.06);box-shadow:0px 0px 8px 0px rgba(0,0,0,0.06)
}
.el-card.is-always-shadow{-webkit-box-shadow:0px 0px 8px 0px rgba(0,0,0,0.06);box-shadow:0px 0px 8px 0px rgba(0,0,0,0.06)
}
input::-webkit-input-placeholder{opacity:1 !important
}
input::-moz-placeholder{opacity:1 !important
}
input::-ms-input-placeholder{opacity:1 !important
}
input::placeholder{opacity:1 !important
}
.el-button [class*=" el-icon-"],.el-button [class^=el-icon-]{vertical-align:top
}
.el-badge__content{border:none
}
.el-card__header{font-size:16px;color:#333
}
#app{width:100%;height:100%;background-color:#f7f8fa
}
#app .nav{width:200px;height:calc(100% - 80px);display:inline-block;overflow-y:auto;vertical-align:top
}
#app .center-views{display:inline-block;width:calc(100% - 243px)
}

body{cursor:pointer
}
#plateNumber{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:50px;font-size:14px;padding:0 20px;background-color:#fff
}
#plateNumber .special-title{color:#000;font-weight:bold
}
#plateNumber .plate-number-title{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;padding:0 20px;color:#000;font-weight:bold
}
#plateNumber .num0{width:25px;height:25px;background-color:#3574F7;color:#fff;border-radius:4px;padding:0 20px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
#plateNumber .plate-number-center{width:130px;height:20px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;background-color:#fff;position:relative
}
#plateNumber .plate-number-center:after{display:block;position:absolute;left:0;bottom:0;width:100%;border-top:1px solid #dedede;content:''
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2){
#plateNumber .plate-number-center:after{-webkit-transform:scale(1, 0.5);transform:scale(1, 0.5)
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
#plateNumber .plate-number-center:after{-webkit-transform:scale(1, 0.33);transform:scale(1, 0.33)
}
}
#plateNumber .plate-number-center .plate-number-inner{min-width:2px;min-height:12px;border-right:1px solid #f1b032;-webkit-animation:myfirst 1s;animation:myfirst 1s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
#plateNumber .plate-number-center .is-border-show{border-right:1px solid transparent;-webkit-animation:none;animation:none
}
#plateNumber .plate-number-center .error-hint{color:red
}
#plateNumber .special-plate{-webkit-box-flex:1;-ms-flex:1;flex:1;text-align:right
}
#plateNumber .special-plate .special-plate-inner{display:inline-block;color:#455a6499
}
@-webkit-keyframes myfirst{
0%{border-right:1px solid #f1b032
}
25%{border-right:1px solid #fff
}
50%{border-right:1px solid #fff
}
100%{border-right:1px solid #f1b032
}
}
@keyframes myfirst{
0%{border-right:1px solid #f1b032
}
25%{border-right:1px solid #fff
}
50%{border-right:1px solid #fff
}
100%{border-right:1px solid #f1b032
}
}

#selectProvince .first-word-wraps{background-color:#D2D5DB;padding:0.1667rem
}
#selectProvince .first-word-wraps .first-word{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-bottom:0.45rem
}
#selectProvince .first-word-wraps .first-word .word{-webkit-box-sizing:border-box;box-sizing:border-box;width:1.2rem;height:1.2rem;color:#000;font-weight:bold;-webkit-box-shadow:0px 1px 4px rgba(0,0,0,0.35);box-shadow:0px 1px 4px rgba(0,0,0,0.35);border-radius:0.16rem;text-align:center
}
#selectProvince .first-word-wraps .first-word .word span{-webkit-box-sizing:border-box;box-sizing:border-box;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;text-align:center;width:100%;height:100%;background-color:#fff;color:#000;border-radius:0.125rem
}
#selectProvince .first-word-wraps .first-word .word .active{background-color:#dedede
}
#selectProvince .first-word-wraps .first-word .last-line{width:1.4rem;height:1.2rem
}
#selectProvince .first-word-wraps .first-word:nth-last-of-type(1){margin-bottom:0rem
}

.number-and-english .first-word-wrap{background-color:#D2D5DB;padding:0.1667rem
}
.number-and-english .first-word-wrap .num-eng-item{-webkit-box-flex:1;-ms-flex:1;flex:1;width:1.2rem;height:1.2rem;color:#000;font-weight:bold;-webkit-box-shadow:0px 1px 4px rgba(0,0,0,0.35);box-shadow:0px 1px 4px rgba(0,0,0,0.35);margin:0.1667rem;background-color:#fff;border-radius:0.1rem;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
.number-and-english .first-word-wrap .active{background-color:#dedede
}
.number-and-english .first-word-wrap .disable-item{color:#dedede
}
.number-and-english .first-word-wrap .number-list{display:-webkit-box;display:-ms-flexbox;display:flex
}
.number-and-english .first-word-wrap .english-list .english-cell{display:-webkit-box;display:-ms-flexbox;display:flex
}

.yj-button{position:relative;display:inline-block;-webkit-box-sizing:border-box;box-sizing:border-box;height:44px;margin:0;padding:0;font-size:16px;line-height:42px;text-align:center;border-radius:2px;cursor:pointer;-webkit-transition:opacity 0.2s;transition:opacity 0.2s;-webkit-appearance:none;-webkit-text-size-adjust:100%
}
.yj-button::before{position:absolute;top:50%;left:50%;width:100%;height:100%;background-color:#000;border:inherit;border-color:#000;border-radius:inherit;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);opacity:0;content:' '
}
.yj-button-normal{padding:0 15px;font-size:14px
}
.yj-button-default{color:#323233;background-color:#fff;border:1px solid #ebedf0
}
.yj-button-primary{color:#fff;background-color:#07c160;border:1px solid #07c160
}
.yj-button-info{color:#fff;background-color:#1989fa;border:1px solid #1989fa
}
.yj-button-danger{color:#fff;background-color:#ee0a24;border:1px solid #ee0a24
}
.yj-button-warning{color:#fff;background-color:#ff976a;border:1px solid #ff976a
}
.yj-button--plain{background-color:#fff
}
.yj-button--plain.yj-button-default{color:#323233;background-color:#fff;border:1px solid #ebedf0
}
.yj-button--plain.yj-button-primary{color:#07c160;background-color:#fff;border:1px solid #07c160
}
.yj-button--plain.yj-button-info{color:#1989fa;background-color:#fff;border:1px solid #1989fa
}
.yj-button--plain.yj-button-danger{color:#ee0a24;background-color:#fff;border:1px solid #ee0a24
}
.yj-button--plain.yj-button-warning{color:#ff976a;background-color:#fff;border:1px solid #ff976a
}

.actionSheet{position:absolute;top:0;left:0;width:100%;height:100%;overflow:hidden
}
.actionSheet .yj-overlayer{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.7)
}
.actionSheet .action-sheet-box{position:fixed;background-color:#fff;border-radius:20px 20px 0 0;width:100%;bottom:0;left:0
}
.actionSheet .action-sheet-box .action-sheet-title{min-height:40px;font-size:18px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
.actionSheet .action-sheet-box .action-sheet-innner{min-height:50px;font-size:14px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
